import headTop from '@/components/base/header/head';
export default {
    name: "allowLogin",
    components: {
        headTop
    },
    data(){
        return{
            url: 123,
            id: 11
        }
    },
    created(){
        this.url = this.$route.path;
        this.id = this.$route.query.id
    },
    methods:{
        yesLogin(){
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.scan_code,
                type: "post",
                data: {
                    client_id: this.id
                },
                success: (response, allResponse) => {
                    console.log(response)
                }
            });
            this.$router.push('/mine')
        }
    }
}



